body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  flex-grow: 1;
}

.main__container {
  @extend %container;
  // min-height: 70vh;
}

.main__list {
  padding: 20px 0;

  @include media(min, tablet) {
    padding: 32px 0;
    @include flex(32px, flex-start, top);
    flex-wrap: wrap;
  }

  @include media(min, desktop) {
    @include flex(15px, flex-start, top);
  }
}

// Стилізація пустої бібліотеки
.clear-list {
  margin: 0 auto;
  text-align: center;
}

.clear-list__title {
  font-weight: 500;
  font-size: 45px;
  line-height: 1.7;
}

.clear-list__text {
  font-size: 30px;
  line-height: 1.7;
}

.clear-list__link {
  display: inline-block;
  margin-top: 20px;
  padding: 13px 0;

  min-width: 150px;
  border: 1px solid var(--black);
  border-radius: 5px;
  background-color: transparent;

  color: var(--black);
  text-align: center;
  text-transform: uppercase;

  transition: $animation;

  &:hover,
  &:focus {
    border-color: var(--orange);
    background-color: var(--orange);
    color: var(--white);
  }
}

// Tui-pagination updated styles
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}
.tui-pagination {
  height: 28px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}
.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 35px;
  padding: 8px 0 6px;
  color: #333;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  vertical-align: middle;
  text-align: center;
  border-radius: 5px;
  border-width: 1px 0;
  @include media(max, phone) {
    width: 25px;
    font-size: 10px;
  }
}
.tui-pagination .tui-page-btn:hover {
  background-color: #f4f4f4;
}
.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background-color: var(--orange);
  cursor: default;
}
.tui-pagination .tui-is-selected:hover {
  background-color: var(--orange);
}
.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 26px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  text-align: center;
}
.tui-pagination .tui-last,
.tui-pagination .tui-first {
  overflow: hidden;
  height: 26px;
  padding: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  text-align: center;
}
.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}
.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}
.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  margin-top: 10px;
}
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}
.tui-pagination .tui-prev {
  margin-right: 10px;
}
.tui-pagination .tui-next {
  margin-left: 10px;
}
.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}
.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}
.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}
.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}
.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}
.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}
.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}
.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}
.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}
.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}
.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}
.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}
.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
}
